* {
   font-family: 'Ubuntu', sans-serif;
   box-sizing: border-box;
   transition: all 0.3s;
   margin: 0;
   padding: 0; }
header {
   nav {
      padding: 0 100px;
      width: 100%;
      height: 92px;
      background: rgba(229, 229, 229, .8);
      position: fixed;
      z-index: 10;
      display: flex;
      justify-content: space-between;
      .header_logo {
        margin-top: 36px;
        margin-right: 60px; }
      .menu {
         display: flex;
         flex-wrap: wrap;
         justify-content: space-between;
         width: 460px;
         margin-top: 33px;
         margin-right: 180px;
         li {
            list-style: none;
            a {
               font-weight: 700;
               font-size: 16px;
               color: #424551;
               text-decoration: none;
               &:hover {
                  color: #ff5a30; } } } }
      .submenu {
         display: flex;
         justify-content: space-between;
         width: 400px;
         margin-top: 22px;
         &_block {
            display: flex;
            justify-content: space-between; }
         &_img {
            margin-right: 15px; }
         &_textblock_title {
            font-weight: 570px;
            font-size: 14px;
            color: #424551; } } } }
.subheader {
   display: flex;
   justify-content: flex-start;
   padding-top: 240px;
   background: url('../img/header_bg.jpg') center (center/cover) no-repeat;
   height: 750px;
   &_block {
      width: 577px; }
   &_title {
      font-weight: 700;
      font-size: 72px;
      line-height: 130%;
      letter-spacing: 1px;
      color: #FFFFFF;
      margin-bottom: 24px;
      span {
         color: #FF5A30; } }
   &_descr {
      font-weight: 400;
      font-size: 20px;
      line-height: 150%;
      color: #fff;
      margin-bottom: 61px; }
   &_buttons {
      width: 526px;
      display: flex;
      justify-content: space-between;
      &_white {
         font-weight: 700;
         font-size: 16px;
         color: #fff;
         width: 260px;
         height: 52px;
         border: 2px solid #fff;
         border-radius: 5px;
         background-color: rgba(255, 255, 255, .0);
         &:hover {
            background-color: rgba(255, 255, 255, .6);
            color: #fff;
            border: 2px solid #fff; } }
      &_orange {
         width: 224px;
         height: 52px;
         background: #FF5A30;
         border-radius: 5px;
         font-weight: 700;
         font-size: 16px;
         color: #fff;
         border: none;
         &:hover {
            background-color: rgb(250, 63, 16);
            border: none;
            color: #fff; } } } }
.title {
   font-weight: 700;
   font-size: 46px;
   text-align: center;
   margin-bottom: 24px; }
.subtitle {
   font-weight: 400;
   font-size: 18px;
   text-align: center;
   margin-bottom: 60px; }
.values {
   padding: 120px 0 70px;
   &_gblock {
      display: flex;
      justify-content: space-between; }
   &_block {
      display: flex;
      flex-direction: column;
      align-items: center;
      width: 285px;
      border: 2px solid rgba(218, 219, 221, .5);
      border-radius: 5px;
      padding: 20px; }
   &_icon {
      width: 48px;
      height: 48px;
      margin-bottom: 24px; }
   &_title {
      font-weight: 700;
      font-size: 20px;
      margin-bottom: 8px; }
   &_descr {
      font-weight: 400;
      font-size: 16px;
      text-align: center; } }
.services {
   min-height: 730px;
   padding: 50px 0 0;
   background: url('../img/bg-image.png') center (center/cover) no-repeat;
   &_block {
      display: flex;
      justify-content: space-between; }
   &_square {
      width: 285px;
      height: 285px;
      background-color: #fff;
      border: 1px solid rgba(204, 204, 204, .3);
      border-radius: 5px;
      box-shadow: 8px 8px 15px rgba(204, 204, 204, .3);
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      &:hover {
         box-shadow: none; } }
   &_ico {
      margin-bottom: 25px; }
   &_descr {
      font-weight: 700;
      font-size: 20px; }
   &_learn {
      display: flex;
      justify-content: center;
      margin: 85px 0;
      &_title {
         font-weight: 700;
         font-size: 28px;
         margin-right: 40px; } } }
.partners {
   background-color: #F4F5F6;
   height: 350px;
   padding-top: 80px;
   &_block {
      display: flex;
      justify-content: space-between; } }
.our {
   background-color: #F4F5F6;
   height: 500px;
   padding-bottom: 120px;
   padding-top: 30px;
   .container {
      position: relative; }
   .title {
      margin-bottom: 60px;
      text-align: left; }
   &_descr {
      margin: 24px 0;
      width: 600px;
      color: #424551; }
   &_title {
      margin-bottom: 4px;
      font-weight: bold;
      font-size: 16px; }
   &_subtitle {
      font-size: 14px;
      color: #787A80; }
   &_infoimg {
      position: absolute;
      right: 0;
      top: -20px; } }
.figures {
   padding: 140px 0 80px;
   height: 520px;
   &_block {
      width: 740px;
      margin: 0 auto;
      margin-top: 60px;
      display: flex;
      justify-content: space-between; }
   &_subtitle {
      margin-top: 20px;
      color: #1E212C;
      font-size: 14px;
      text-align: center; } }
.details {
   height: 826px;
   background: url('../img/footer_bg.jpg') center (center/cover) no-repeat;
   padding: 80px 0;
   .container {
      position: relative; }
   &_quick {
      position: absolute;
      right: 0;
      width: 495px;
      height: 685px;
      background-color: #fff;
      padding: 48px 40px; }
   &_title {
      text-align: center;
      font-weight: 700;
      font-size: 28px;
      color: #1E212C;
      margin-bottom: 24px; }
   &_form {
      display: flex;
      flex-direction: column;
      label {
         display: block;
         font-size: 14px;
         margin-bottom: 8px; }
      input {
         margin-bottom: 20px;
         width: 415px;
         height: 44px;
         background: #F4F5F7;
         border: 1px solid #D7DADD;
         border-radius: 4px;
         padding-left: 16px; }
      .checkbox {
         display: inline-block;
         width: 16px;
         height: 16px;
         margin-bottom: 0; }
      textarea {
         display: block;
         width: 415px;
         height: 66px;
         resize: none;
         padding-left: 16px;
         padding-top: 11px;
         background: #F4F5F7;
         border: 1px solid #D7DADD;
         border-radius: 4px;
         margin-bottom: 23px; }
      .label_checkbox {
         width: 350px;
         font-size: 14px; }
      .details_orange {
         width: 174px;
         height: 44px;
         font-weight: 700;
         font-size: 14px;
         background: #FF5A30;
         color: #fff;
         display: block;
         margin: 0 auto;
         margin-top: 24px;
         border: none;
         &:hover {
            background-color: rgb(250, 63, 16);
            border: none;
            color: #fff; } } } }
.footer {
   height: 450px;
   padding: 80px 0 40px;
   background: #1E212C;
   &_block_1 {
      display: flex;
      justify-content: space-between; }
   &_minblock_1 {
      width: 420px;
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between; }
   &_logo {
      display: block;
      margin-right: 62px;
      width: 142px;
      height: 24px; }
   &_ico_block {
      img {
         margin-right: 15px;
         &:last-child {
            margin-right: 0; } } }
   &_descr_1 {
      width: 493px;
      color: rgba(255, 255, 255, .6);
      font-size: 14px;
      margin-top: 20px; }
   &_title {
      font-size: 24px;
      color: #fff;
      font-weight: 700;
      margin-bottom: 24px; }
   &_minblock_2 {
      width: 495px;
      form {
         display: flex;
         input {
            width: 364px;
            background: rgba(255, 255, 255, 0.12);
            border: 1px solid rgba(255, 255, 255, 0.2);
            border-radius: 4px;
            height: 44px;
            padding-left: 16px; } } }
   &_orange {
      width: 131px;
      height: 44px;
      background: #FF5A30;
      border-radius: 0px 4px 4px 0px;
      border: none;
      color: #fff;
      font-weight: 700;
      font-size: 14px;
      &:hover {
            background-color: rgb(250, 63, 16);
            border: none;
            color: #fff; } }
   &_descr_2 {
      font-weight: 400;
      font-size: 12px;
      color: rgba(255, 255, 255, .6);
      margin-top: 16px; }
   &_block_2 {
      display: flex;
      justify-content: space-between;
      margin-top: 49px; }
   &_head {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      &_descr {
         font-weight: 400;
         font-size: 16px;
         color: rgba(255, 255, 255, .6);
         span {
            color: #fff; } } }
   &_b2title {
      font-size: 16px;
      font-weight: 700;
      color: #fff;
      letter-spacing: 1px;
      margin-bottom: 13px; }
   .on {
      margin-left: 30px; }
   &_who {
      ul {
         list-style: none;
         li {
            a {
               text-decoration: none;
               color: rgba(255, 255, 255, .6);
               &:hover {
                  color: #fff;
                  text-decoration: underline; } } } } } }



